import React, { lazy, Suspense } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Footer from './components/Footer';
import Header from './components/Header';
import PopUp from './components/PopUp';
import Preloader from './components/Preloader';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import AnimatedCursor from 'react-animated-cursor';

const Main = lazy(() => import('./pages/Main'));
const MoreOurProjects = lazy(() => import('./pages/MoreOurProjects'));
const ProjectsInfo = lazy(() => import('./pages/ProjectsInfoPage'));

function App() {
  return (
    <Router>
      <Suspense fallback={<Preloader />}>
        <AnimatedCursor
          innerSize={8}
          outerSize={35}
          color="0, 0, 0"
          outerAlpha={1}
          innerScale={1}
          outerScale={2}
          outerStyle={{
            backgroundColor: 'white',
            mixBlendMode: 'exclusion',
            zIndex: '9999999',
          }}
          clickables={[
            'a',
            'input[type="text"]',
            'input[type="email"]',
            'input[type="number"]',
            'input[type="submit"]',
            'input[type="image"]',
            'label[for]',
            'select',
            'textarea',
            'button',
            '.link',
            '.grid-item',
          ]}
        />
        <Header />
        <Routes>
          <Route exact path="/bureaux/" element={<Main />} />
          <Route path="/bureaux/projects" element={<MoreOurProjects />} />
          <Route path="/bureaux/projects/info/:id" element={<ProjectsInfo />} />
        </Routes>
        <PopUp />
        <Footer />
        <ToastContainer />
      </Suspense>
    </Router>
  );
}

export default App;
